import {
    // 
    SIGNUP,
    SIGNUP_FAIL,
    SIGNUP_SUCCESS,
    RESET_SIGNUP,
    // 
    SIGNIN,
    SIGNIN_FAIL,
    SIGNIN_SUCCESS,
    RESET_SIGNIN,
    // 
    SENDSMS,
    SENDSMS_SUCCESS,
    SENDSMS_FAIL,
} from './constants'
import { IAuthUnionType, IAuthState } from './type'

// state 
const initialState: IAuthState = {
    signup: {
        loaded: false, // 
        success: false, // 
        message: '', // 
    },
    signin: {
        loaded: false, // 
        success: false, // 
        message: '', // 
    },
    sendSms: {
        loaded: false, // 
        success: false, // 
        message: '', // 
    },
}

export default function authReducer(
    state = initialState,
    action: IAuthUnionType,
) {
    switch (action.type) {
        // 
        case SIGNUP:
            return {
                ...state,
                signup: {
                    loaded: false,
                    success: false,
                },
            }

        // 
        case SIGNUP_SUCCESS:
            return {
                ...state,
                signup: {
                    loaded: true,
                    success: true,
                },
            }

        // 
        case SIGNUP_FAIL:
            return {
                ...state,
                signup: {
                    loaded: true,
                    success: false,
                    message: action.message,
                },
            }

        // 
        case RESET_SIGNUP:
            return {
                ...state,
                signup: {
                    loaded: false,
                    success: false,
                    message: '',
                },
            }
        /* ======================================================== */

        // 
        case SIGNIN:
            return {
                ...state,
                signin: {
                    loaded: false,
                    success: false,
                    message: '',
                },
            }
        // 
        case SIGNIN_SUCCESS:
            return {
                ...state,
                signin: {
                    loaded: true,
                    success: true,
                    message: '',
                },
            }
        // 
        case SIGNIN_FAIL:
            return {
                ...state,
                signin: {
                    loaded: true,
                    success: false,
                    message: action.message,
                },
            }

        // 
        case RESET_SIGNIN:
            return {
                ...state,
                signup: {
                    loaded: false,
                    success: false,
                    message: '',
                },
            }

        /* =================================================================== */

        // 
        case SENDSMS:
            return {
                ...state,
                sendSms: {
                    loaded: false,
                    success: false,
                    message: '',
                },
            }

        // 
        case SENDSMS_SUCCESS:
            return {
                ...state,
                sendSms: {
                    loaded: true,
                    success: true,
                    message: '',
                },
            }

        // 
        case SENDSMS_FAIL:
            return {
                ...state,
                sendSms: {
                    loaded: true,
                    success: false,
                    message: action.message,
                },
            }

        default:
            return state
    }
}
