import React, { memo } from 'react'

// assets
import logo from '@/assets/img/logo.png'

// 
import { Space, Image, Divider } from 'antd'
import Card from './styled'

const Logo = memo(() => {
    return (
        <Space direction='vertical'>
            <Space>
                <Image
                    src={logo}
                    alt='this app`s logo that consist of three tiger dolls.'
                    height='32px'
                    preview={false}
                />
                <span className='community-name'></span>
            </Space>
            <p className='community-introduction'>
                
            </p>
            <p className='community-introduction'>
                
            </p>
        </Space>
    )
})

export default memo(() => {
    return (
        <Card className='community' title={<Logo />}>
            <Space
                align='center'
                size='large'
                style={{
                    display: 'flex',
                    justifyContent: 'space-evenly',
                }}
            >
                <div>
                    <div className='count-title'></div>
                    <div className='count-data'> 1487</div>
                </div>
                <Divider className='divider' type='vertical' />
                <div>
                    <div className='count-title'></div>
                    <div className='count-data'>2760</div>
                </div>
            </Space>
        </Card>
    )
})
