// 
import React, { memo, FC, useState, useEffect } from 'react'

// redux 
import { useDispatch, useSelector } from 'react-redux'
import IAppState from '@/store/type'
import { lightSignup, resetSignup } from '@/store/auth/actionCreators'
import { IAuthState, ISignupPayload } from '@/store/auth/type'

// 
import StyledModal from './styled'
import { Form, Input, message } from 'antd'

const formItemLayout = {
    labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
    },
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
    },
}

// 
/* const validateMobile = (obj: any, value: any) => {
    if (!value) {
        return Promise.resolve()
    }

    const phoneReg = /^1[3456789]\d{9}$/
    if (!phoneReg.test(value)) {
        return Promise.reject('')
    }

    return Promise.resolve()
} */

// signup 
interface signupProps {
    visible: boolean
    setVisible: (values: boolean) => void
    jumpToIndex: () => void
}

const signup: FC<signupProps> = memo(props => {
    // 
    const { visible, setVisible, jumpToIndex } = props

    // 
    const [isDisabled, setIsDisabled] = useState<boolean>(true)
    const [confirmLoading, setConfirmLoading] = useState<boolean>(false)

    //  dispatch 
    const dispatch = useDispatch()

    // 
    const auth = useSelector<IAppState, IAuthState>(state => state.auth)

    // 
    const [form] = Form.useForm()

    const handleOk = () => {
        setConfirmLoading(true)
        form.validateFields()
            .then(values => {
                createUser(values)
            })
            .catch(info => {
                console.log(info)
                message.error(`${info.errorFields[0].errors[0]}`, 3)
                setConfirmLoading(false)
            })
    }

    // 
    const createUser = (value: ISignupPayload) => {
        // 
        dispatch(lightSignup(value))
    }

    // 
    useEffect(() => {
        // 1. ,  loading  index 
        if (auth.signup.loaded && auth.signup.success) {
            form.resetFields()
            setConfirmLoading(false)
            jumpToIndex()
        }

        // 2.  
        if (auth.signup.loaded && !auth.signup.success) {
            setConfirmLoading(false)
            message.error(auth.signup.message)
        }
    }, [auth.signup, form, jumpToIndex])

    // 3. 
    useEffect(() => {
        return () => {
            dispatch(resetSignup())
        }
    }, [dispatch])

    return (
        <StyledModal
            title=''
            cancelText=''
            okText=''
            okButtonProps={{ htmlType: 'submit', disabled: isDisabled }}
            visible={visible}
            confirmLoading={confirmLoading}
            onCancel={() => setVisible(false)}
            onOk={() => handleOk()}
        >
            <Form
                {...formItemLayout}
                form={form}
                name='signup'
                scrollToFirstError
            >
                <Form.Item
                    name='username'
                    label=''
                    tooltip='?'
                    rules={[
                        {
                            required: true,
                            message: '',
                            whitespace: true,
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    name='password'
                    label=''
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                    ]}
                    hasFeedback
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item
                    name='confirm'
                    label=''
                    dependencies={['password']}
                    hasFeedback
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                        /* bug setIsDisabled() */
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (
                                    !value ||
                                    getFieldValue('password') === value
                                ) {
                                    setIsDisabled(false)
                                    return Promise.resolve()
                                }
                                return Promise.reject(
                                    new Error(''),
                                )
                            },
                        }),
                    ]}
                >
                    <Input.Password />
                </Form.Item>

                {/* <Form.Item
                    name='email'
                    label=''
                    rules={[
                        {
                            type: 'email',
                            message: '',
                        },
                        {
                            required: true,
                            message: '',
                        },
                    ]}
                >
                    <Input />
                </Form.Item> */}

                {/* <Form.Item
                    name='phone'
                    label=''
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                        {
                            validator: validateMobile,
                        },
                    ]}
                >
                    <Input style={{ width: '100%' }} />
                </Form.Item>

                <Form.Item
                    name='intro'
                    label=''
                    rules={[{ required: true, message: '' }]}
                >
                    <Input.TextArea showCount maxLength={100} />
                </Form.Item> */}
            </Form>
        </StyledModal>
    )
})

export default signup
